"use client";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

export default function SmartSubQueryForm({
  category,
  typeName,
  catalog,
  rows,
  updateRow,
  addRow,
  removeRow,
}: {
  category: string;
  typeName: string;
  catalog: Record<string, any>;
  rows: any[];
  updateRow: (idx: number, field: string, val: string) => void;
  addRow: () => void;
  removeRow: (idx: number) => void;
}) {
  const problems = catalog[category][typeName].problems || [];

  return (
    <div className="space-y-3">
      <h2 className="text-xl font-semibold">2. Describe issues (smart)</h2>

      {rows.map((r, i) => (
        <Card key={i} className="border">
          <CardContent className="grid grid-cols-1 md:grid-cols-4 gap-3 items-start py-4">
            <select
              className="py-2 px-3 border rounded-md"
              value={r.problem}
              onChange={(e) => updateRow(i, "problem", e.target.value)}
            >
              <option value="">Select problem</option>
              {problems.map((p: string) => (
                <option key={p} value={p}>
                  {p}
                </option>
              ))}
            </select>

            <Input
              placeholder="Speed (e.g., 60 km/h)"
              value={r.speed}
              onChange={(e) => updateRow(i, "speed", e.target.value)}
            />

            <Textarea
              placeholder="Additional notes"
              value={r.notes}
              onChange={(e) => updateRow(i, "notes", e.target.value)}
            />

            <div className="flex gap-2">
              <Button onClick={() => removeRow(i)} variant="destructive">Remove</Button>
              {i === rows.length - 1 && <Button onClick={addRow}>Add</Button>}
            </div>
          </CardContent>
        </Card>
      ))}
    </div>
  );
}
